.navbar .correct {
	-webkit-animation: .4s correct;
	        animation: .4s correct;
}

.navbar .incorrect {
	-webkit-animation: .4s incorrect;
	        animation: .4s incorrect;
}

@-webkit-keyframes correct {
	0% {
		color: white;
	}

	50% {
		color: blue;
	}

	100% {
		color: white;
	}
}

@keyframes correct {
	0% {
		color: white;
	}

	50% {
		color: blue;
	}

	100% {
		color: white;
	}
}

@-webkit-keyframes incorrect {
	0% {
		color: white;
	}

	50% {
		color: red;
	}

	100% {
		color: white;
	}
}

@keyframes incorrect {
	0% {
		color: white;
	}

	50% {
		color: red;
	}

	100% {
		color: white;
	}
}

.navbar {
	background: blue;
	min-height: 70px;
	position: fixed;
	width: 100%;
	color: white;
	z-index: 99;
	white-space: nowrap;
}

.navbar ul {
	margin: 0;
	padding: 0;
}

.navbar li {
	display: inline-block;
	padding: 26px 0px;
	width: 33.3%;
	text-align: center;
}

.navbar a {
	text-decoration: none;
	color: white;
	font-weight: 900;
}

@media (max-width: 420px) {
	.navbar li:first-child {
		width: 25%;
	}

	.navbar li:nth-child(2) {
		width: 35%;
	}

	.navbar li:last-child {
		width: 40%;
	}
}

.header {
	background-color: black;
	background-image: url("/ClickGame/client/public/assets/images/geometry.svg");
	background-repeat: repeat;
	color: white;
	min-height: 400px;
	padding: 180px 20px;
	padding-bottom: 140px;
	text-align: center;
}

.header h1 {
	margin: 0;
	margin-bottom: 30px;
	font-size: 4rem;
}

.container {
	display: flex;
	min-height: 80%;
	flex-wrap: wrap;
	justify-content: center;
	padding-top: 50px;
	padding-bottom: 50px;
	width: 90%;
	margin: 0 auto;
}

@media (min-width: 1100px) {
	.container {
		padding-left: 100px;
		padding-right: 100px;
		width: 1100px;
	}
}

.click-item {
	background-size: cover;
	background-position: center;
	height: 170px;
	width: 170px;
	margin: 8px 25px;
	border-radius: 3px;
	cursor: pointer;
}

.click-item:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
	transition: 0.1s;
}

.shake {
	-webkit-animation: shake .5s 0.0125s;
	        animation: shake .5s 0.0125s;
}

@-webkit-keyframes shake {
	from,
	to {
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}

	25%,
	50%,
	75% {
		-webkit-transform: translate(10px);
		        transform: translate(10px);
	}
}

@keyframes shake {
	from,
	to {
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
	}

	25%,
	50%,
	75% {
		-webkit-transform: translate(10px);
		        transform: translate(10px);
	}
}

@media (max-width: 750px) {
	.click-item {
		margin: 10px;
	}
}

@media (max-width: 500px) {
	.click-item {
		margin: 10px;
	}
}

.footer {
	background: blue;
	height: 100px;
	position: relative;
	color: white;
	overflow: auto;
}

.footer > .bottom {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	background: blue;
	padding: 8px;
	font-weight: 600;
}

.footer > .bottom > img {
	width: 20px;
	position: relative;
	top: 3px;
	left: 2px;
}

* {
	box-sizing: border-box;
	font-family: sans-serif;
}
html,
body,
#root,
#root > div {
	margin: 0;
	padding: 0;
	height: 100%;
}

